<template>
  <div>

<nav-bar :name="title"></nav-bar>

      <v-container class="text-center">
      <v-card class="ma-auto" width="90%" outlined>
        <br>
        <v-row>
            <v-col cols="12">
                <h5 class="text-left ml-2"> SEND EMAIL </h5>
                <v-divider class="ma-2"></v-divider>
              <v-select
                outlined
                dense
                placeholder="STORES"
                class="mx-4 mb-n6"
                v-model="selected"
                chips
                :items="items"
                multiple
              >
                <template v-slot:prepend-item>
                  <v-list-item
                    ripple
                    @click="toggle"
                  >
                    <v-list-item-action>
                      <v-icon :color="selected.length > 0 ? '#E49711' : ''">
                        {{ icon }}
                      </v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                      <v-list-item-title>
                        Select All
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider class="mt-2"></v-divider>
                </template>
              </v-select>
            </v-col>
            <v-col cols="12">
                <h5 class="text-left ml-2"> Subject: </h5>
                <v-text-field
                outlined
                dense
                class="mx-4 mb-n6"
                v-model="email.subject"
                >
                </v-text-field>
            </v-col>
            <v-col cols="12">
                <h5 class="text-left ml-2"> Message: </h5>
                <v-textarea
                outlined
                dense
                class="mx-4"
                v-model="email.message"
                >
                </v-textarea>
            </v-col>
            
            <v-col>
            <v-btn
            class="mx-4 mb-6"
            color="#989898"
            dark
            width="30%"
            @click="$router.go(-1)"
            >
            BACK
            </v-btn>
            <v-btn
            class="mx-4 mb-6"
            color="#E47911"
            dark
            width="30%"
            :loading="loading"
            @click="send"
            >
            SEND
            </v-btn>
            </v-col>
        </v-row>
      </v-card>
      </v-container>

  </div>
</template>

<script>
  import NavBar from '@/components/NavBar';
  import StoreService from '@/services/StoreService';
  import Swal from 'sweetalert2';

  export default {
    components: {
      NavBar
    },
    data: () => ({
      title: 'EMAIL',
      loading: false,
      items: [

      ],
      selected: [],
      email:{
        subject: '',
        message: '',
        emails: [],
      }
    }),

    computed: {
      likesAll () {
        return this.selected.length === this.items.length
      },

      likesSome () {
        return this.selected.length > 0 && !this.likesAll
      },

      icon () {
        if (this.likesAll) return 'mdi-close-box'
        if (this.likesSome) return 'mdi-minus-box'
        return 'mdi-checkbox-blank-outline'
      },

      data_emails () {
        if(this.selected.length > 1){
          let email = [];
          this.selected.forEach(item => {
            email.push( item.value )
          })
          return email;
        }
        else {
          return this.selected;
        }
      },
    },

    mounted() {
      StoreService
      .list_admin()
      .then(res => {
        for (let i = 0; i < res.length; i++) {
              let store = res[i];
              this.items.push({ text: store.name, value: store.email })
          }
      })
      .catch(e => {
        let error = e.response.data;
        let msg = ''
        Object.entries(error).forEach(([ key, value ]) => {
            msg+= key + ': ' + value + ' \n ';
        });
            this.loading = false;
            Swal.fire({
            icon: 'error',
            title: 'Error',
            text: msg
            });
        })
    },

    methods: {
      toggle () {
        this.$nextTick(() => {
          if (this.likesAll) {
            this.selected = []
          } else {
            this.selected = this.items.slice()
          }
        })
      },

      send(){
        this.loading = true;
        this.email.emails = this.data_emails;
        StoreService
          .send_email(this.email)
          .then(res => {
            Swal.fire({
              icon: 'success',
              title: 'Success',
              text: res['message']
            }).then(() => {
                this.loading = false
                this.$router.push('/dashboard')
              })
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
      }
    },

  }
</script>